export const REQUEST_PLAYER = "REQUEST_PLAYER";
export const ENTER_NOTEMODE = "ENTER_NOTEMODE";
export const ENTER_PLAYMODE = "ENTER_PLAYMODE";
export const EXIT_PLAYMODE = "EXIT_PLAYMODE";
export const RESIZE_PLAYER = "RESIZE_PLAYER";
export const RESET_PLAYER = "RESET_PLAYER";
export const START_PLAYER = "START_PLAYER";
export const STOP_PLAYER = "STOP_PLAYER";

export const DRAW_GAME_PIANO = "DRAW_GAME_PIANO";
export const DRAW_NOTE_PIANO = "DRAW_NOTE_PIANO";
export const DRAW_SEEKBAR = "DRAW_SEEKBAR";
export const DRAW_FINGER = "DRAW_FINGER";
export const DRAW_NOTES = "DRAW_NOTES";
export const DRAW_GAME = "DRAW_GAME";

export interface IMelodition {
  playMode: boolean,
  noteMode: boolean,
  loading: boolean,
  octave: number,
  fingerLoading: boolean,
  pianoLoading: boolean,
  notesLoading: boolean,
  gameLoading: boolean,
  seekLoading: boolean,
  tapSize: number,
  songLength: number,
  canvasSize?: ICanvas,
  neededLines?: INeededLine,
  neededTracks?: INeededTrack[],
  pianoSize?: IPianoSizes[],
  handRefs?: HTMLCollection,
  noteInfo?: INoteDraw,
}
export interface DrawGamePiano {
  type: typeof DRAW_GAME_PIANO;
  payload?: {
    pianoSize: IPianoSizes[]
    canvasSize: ICanvas
    tapSize: number
  }
}
export interface DrawNotePiano {
  type: typeof DRAW_NOTE_PIANO;
  payload: { noteInfo: INoteDraw }
}
export interface DrawGame {
  type: typeof DRAW_GAME;
}
export interface DrawNotes {
  type: typeof DRAW_NOTES;
}
export interface DrawFinger {
  type: typeof DRAW_FINGER;
}
export interface DrawSeekbar {
  type: typeof DRAW_SEEKBAR;
}

export interface StartPlayers {
  type: typeof START_PLAYER;
}
export interface StopPlayers {
  type: typeof STOP_PLAYER;
}
export interface ResizePlayers {
  type: typeof RESIZE_PLAYER;
}

export interface ResetPlayers {
  type: typeof RESET_PLAYER;
}
export interface RequestPlayers {
  type: typeof REQUEST_PLAYER;
  payload: {
    songLength: number
    neededLines: INeededLine
    neededTracks: any[]
  };
}
export interface EnterNoteMode {
  type: typeof ENTER_NOTEMODE;
}
export interface EnterPlayMode {
  type: typeof ENTER_PLAYMODE;
}
export interface ExitPlayMode {
  type: typeof EXIT_PLAYMODE;
}

export type PlayerActionTypes =
  | DrawSeekbar
  | DrawFinger
  | DrawNotes
  | DrawGame
  | DrawNotePiano
  | DrawGamePiano
  | ResizePlayers
  | RequestPlayers
  | StartPlayers
  | StopPlayers
  | ResetPlayers
  | EnterNoteMode
  | EnterPlayMode
  | ExitPlayMode;

export interface PlayerDataInfo {
  fingers: string[];
  song: {
    content: string;
    edited: string;
    id: number;
    name: string;
  }
  track: {
    id: number;
    name: string;
  }
}

export interface IQueue {
  target: any,
  preset: any,
  when: any,
  pitches: any,
  duration: any,
  volume: any,
  slides?: any
}
export interface IEnvelope {
  envelope: any,
  zone: any,
  volume: any,
  startWhen: any,
  waveDuration: any,
  duration: any
}
export interface ISetup {
  target: any,
  when: any,
  duration: any
}
export interface INotePos {
  [key: number]: {
    pos: number;
    note: string
  }
}
export interface INoteDraw {
  width: number,
  height: number,
  tapSize: number,
  spaceSize: number,
  fontSize: number,
  linesArray: number[],
  notePos: INotePos,
}
export interface INeededTrack {
  beat: number,
  pitch: number,
  shift: number,
  length: number,
}
export interface INeededLine {
  up: number;
  number: number;
  down: number;
  notes: number;
}
export interface IPianoSizes {
  left: number,
  center: number,
  width: number
  key: string
}
export interface ICanvas {
  width: number;
  height: number;
  baseLine: number;
  pianoLeft: number;
}