import { INotePos } from "../../_types/player.types";
import { ExerciseNote, IOutLineProps } from "./Canvas";

export default class Notes {
    public counter = 0
    public tapSize: number
    public spaceSize: number
    private width: number;
    private height: number;
    private notePos: INotePos = {};
    private fullNotes: ExerciseNote[] = [];
    private context: CanvasRenderingContext2D;
    constructor(canvas: HTMLCanvasElement) {
        this.width = canvas.width;
        this.height = canvas.height;
        this.context = canvas.getContext("2d")!;
    }
    assigner = (options: Partial<any>) => {
        Object.assign(this, options);
    }
    playGame = (num: number, color: string) => {
        this.clear();
        this.updateFullSong(num, color)
    }
    clear = () => {
        this.context.clearRect(0, 0, this.width, this.height);
    }
    drawNote = (showTrack: any[]) => {
        const gap = this.spaceSize * 1.6;
        let r = this.tapSize * 0.46;
        r = this.spaceSize < 20 ? this.spaceSize * 0.42 : r;
        let memo = "K";
        let outline: IOutLineProps;
        let noteMemo: { note: string, text: string }[] = [{ note: 'K', text: "!" }];
        showTrack.forEach((n, i) => {
            const x = gap + i * this.spaceSize;
            const pitch = n.pitch + 36;
            let y = this.notePos[pitch].pos;
            let note = this.notePos[pitch].note;
            let prevNote = this.notePos[pitch - 1];
            let text;
            if (note.length === 2) {
                let prevState = noteMemo.find(o => o.note === note);
                if (prevState) {
                    text = prevState.text;
                    note = prevState.note;
                } else
                    if (!prevNote.note.includes(memo) && i !== 0) {
                        text = "♯";
                        note = prevNote.note;
                        y = prevNote.pos;
                    } else {
                        text = "♭";
                    }
                noteMemo.push({ note, text })
            }
            if (pitch === 81 || pitch === 60 || pitch === 61 || pitch === 40) {
                outline = { r: r * 1.5, x, y }
            }
            if (pitch === 59 || pitch === 58) {
                outline = { r: r * 1.2, x, y: this.notePos[60].pos }
            }
            memo = note;
            const notes = new ExerciseNote({ x, y, r, pitch, text, note, outline });
            notes.update(this.context);
            this.fullNotes.push(notes)
        });
    }
    resetGame() {
        this.clear();
        this.fullNotes.forEach(note => {
            note.color = "white";
            note.update(this.context);
        })
    }
    updateFullSong = (num: number, color: string) => {
        this.fullNotes.forEach((note, i) => {
            if (i === num)
                note.color = color;
            if (i === num - 1)
                note.color = "green";
            note.update(this.context);
        });
    }
};