import React, { lazy, Suspense } from "react";
import { Route, Switch } from "react-router-dom";
import { LoadingCircle } from "../components/Layout/Loadings";

import ProtectedRoute from "./ProtectedRoute";
import About from "../components/InfoPages/About";
import PostList from "../components/PostPages/PostList";
import LoginPage from "../components/AuthPages/LoginPage";
import LostPassPage from "../components/AuthPages/LostPage";
import RegisterPage from "../components/AuthPages/RegisterPage";
import Dashboard from "../components/UserPages/Dashboard";

const LearningPage = lazy(() => import("../components/LearningPages"));
const Post = lazy(() => import("../components/PostPages"));
const Guide = lazy(() => import("../components/LearningPages/Guide"));
const Course = lazy(() => import("../components/LearningPages/Course"));
const Lesson = lazy(() => import("../components/LearningPages/Lesson"));
const Exercise = lazy(() => import("../components/LearningPages/Exercise"));

const AppRouter = () => (
  <Switch>
    <Suspense fallback={<LoadingCircle />}>
      <Route exact path={["/", "/post"]} component={PostList} />
      <Route exact path="/learning" component={LearningPage} />
      <Route
        exact
        path={["/guide", "/course", "/exercise", "/favorite", "/lesson"]}
        component={LearningPage}
      />
      <Route exact path="/post/:id" component={Post} />
      <Route exact path="/guide/:id" component={Guide} />
      <Route
        exact
        path={["/course/:id", "/guide/:id/course=:id"]}
        component={Course}
      />
      <Route
        exact
        path={[
          "/lesson/:id",
          "/course/:id/lesson=:id",
          "/guide/:id/lesson=:id",
          "/guide/:id/course=:id/lesson=:id",
        ]}
        component={Lesson}
      />
      <Route
        exact
        path={[
          "/exercise/:id",
          "/course/:id/exercise=:id",
          "/guide/:id/exercise=:id",
          "/guide/:id/course=:id/exercise=:id",
        ]}
        component={Exercise}
      />
      <Route exact path="/login" component={LoginPage} />
      <Route exact path="/register" component={RegisterPage} />
      <Route exact path="/lostpassword" component={LostPassPage} />
      <ProtectedRoute
        exact
        path="/dashboard"
        component={Dashboard}
        isAllowed={true}
        restrictedPath="/dashboard"
        authenticationPath="/login"
      />
      <Route exact path="/about" component={About} />
    </Suspense>
  </Switch>
);

export default AppRouter;
