import MidiState from "../../_helpers/midiContext";
import { BackMover, MusicLine, LessonNote } from "./Canvas";
import { IPianoSizes } from "../../_types/player.types";

export default class Game {
  public counter = 0;
  public tapSize: number = 32;
  public songLength: number = 17;
  private gap: number;
  private margin: number = 128;
  private width: number;
  private height: number;
  private notes: LessonNote[] = [];
  private image: HTMLImageElement;
  private backMover: BackMover[] = [];
  private barNotes: LessonNote[][] = [];
  private fullNotes: LessonNote[] = [];
  private context: CanvasRenderingContext2D;
  constructor(canvas: HTMLCanvasElement) {
    this.image = new Image();
    this.width = canvas.width;
    this.height = canvas.height;
    this.context = canvas.getContext("2d")!;
  }
  assigner = (options: Partial<any>) => {
    console.log(options)
    Object.assign(this, options);
  }
  playGame = (y: number) => {
    this.clear();
    this.updateBacks(y)
    this.updateNotes(y)
  }
  clear = () => {
    this.context.clearRect(0, 0, this.width, this.height);
  }
  pitchName = (pitch: number) => {
    let n = pitch % 12;
    let t = "C";
    if (n === 1) {
      t = "Db";
    }
    if (n === 2) {
      t = "D";
    }
    if (n === 3) {
      t = "Eb";
    }
    if (n === 4) {
      t = "E";
    }
    if (n === 5) {
      t = "F";
    }
    if (n === 6) {
      t = "Gb";
    }
    if (n === 7) {
      t = "G";
    }
    if (n === 8) {
      t = "Ab";
    }
    if (n === 9) {
      t = "A";
    }
    if (n === 10) {
      t = "Bb";
    }
    if (n === 11) {
      t = "B";
    }
    return "" + t;
  }
  drawImage = () => {
    const myLine = document.createElement("canvas").getContext("2d")!;
    myLine.canvas.width = this.width;
    myLine.canvas.height = this.height;
    myLine.save();
    Array.from(Array(17), (_, i) => i).forEach((_, v) => {
      const lineWidth =
        v % 16 === 0
          ? 1.5
          : v % 8 === 0
            ? 1.2
            : v % 4 === 0
              ? 1
              : v % 2 === 0
                ? 0.8
                : 0.5;
      const strokeStyle =
        v % 16 === 0
          ? "black"
          : v % 8 === 0
            ? "black"
            : v % 4 === 0
              ? "rgba(0,0,0,0.5)"
              : v % 2 === 0
                ? "rgba(0,0,0,0.5)"
                : "rgba(0,0,0,0.2)";
      const yPos = v * this.tapSize;
      const line = new MusicLine({
        width: this.width,
        color: strokeStyle,
        x: 0,
        y: yPos,
        lw: lineWidth,
      });
      line.draw(myLine);
    });
    this.image.src = myLine.canvas.toDataURL("image/png");
    let firstLine: BackMover;
    this.image.onload = () => {
      if (!firstLine) {
        firstLine = new BackMover(0, this.height, this.margin);
        firstLine.update(this.context, this.image);
      }
    };
  }
  drawNote = (showTrack: any[], notePos: IPianoSizes[]) => {
    const tap = this.tapSize;
    Array.from(Array(this.songLength), (_, i) => i).forEach(m => {
      const r = notePos[1].width;
      const y = tap * (16 * m);
      this.barNotes[m] = [];
      showTrack.forEach((p) => {
        if (p.beat >= m * 16) {
          if (p.beat < (m + 1) * 16) {
            const yy = y + tap * (p.beat % 16);
            const cc = notePos[p.pitch].center;
            const le = yy + tap * p.length;
            const note = new LessonNote({
              width: r,
              height: le,
              color: "yellow",
              x: cc,
              y: yy,
              end: le + this.height * 3,
              text: this.pitchName(p.pitch),
              note: p.pitch + 36,
              gap: this.gap,
            });
            this.barNotes[m].push(note);
            this.fullNotes.push(note);
          }
        }
      });
    });
  }
  drawBack = () => {
    this.backMover = [];
    for (let i = 0; i < 3; i++) {
      this.backMover.push(new BackMover(i, this.height, this.margin));
    }
  }
  pushNote = (num?: number) => {
    if (num) this.counter = num;
    if (
      this.barNotes[this.counter] &&
      this.barNotes[this.counter].length > 0
    ) {
      this.barNotes[this.counter].forEach((g) => {
        this.notes.push(g);
      });
    }
    if (this.songLength > this.counter + 3)
      this.backMover.push(new BackMover(this.counter + 3, this.height, this.margin));
    this.counter++;
  }
  updateNotes = (y: number) => {
    this.notes.forEach((note, i) => {
      note.y = y;
      note.update(this.context);
      if (note.end < y) {
        this.notes.splice(i, 1);
        // missing notes
        // if (!note.hit)
        // 	this.score.push([note.note, 'miss'])
      }
      if (this.gap - note.y <= note.start) {
        let hit: string;
        MidiState.touchedKeys.forEach((touch) => {
          if (note.note === touch.note) {
            if (
              touch.y > note.beg - this.tapSize &&
              touch.y < note.beg + this.tapSize
            ) {
              note.hit = y - this.gap + this.tapSize * 0.1;
              if (
                touch.y - this.tapSize * 0.3 <= note.beg &&
                touch.y + this.tapSize * 0.3 >= note.beg
              ) {
                hit = "perfect";
                note.color = "green";
              } else if (touch.y > note.beg) {
                hit = "late";
                note.color = "purple";
              } else if (touch.y < note.beg) {
                hit = "early";
                note.color = "orange";
              }
              if (!touch.hit) {
                touch["hit"] = hit;
                MidiState.score.push([note.note, touch.hit]);
                console.log(MidiState.score);
              }
            }
          }
        });
        note.move();
      }
    });
  }
  updateBacks = (y: number) => {
    this.backMover.forEach((back, i) => {
      back.y = y;
      back.update(this.context, this.image);
      if (back.y > back.end) {
        this.backMover.splice(i, 1);
      }
    });
  }
  updateFullSong = (y: number) => {
    this.clear();
    Array.from(Array(this.songLength), (_, i) => i).forEach((_, m) => {
      this.context.drawImage(this.image, 0, Math.floor(y - m * this.image.height - this.tapSize * 4));
    });
    this.fullNotes.forEach(note => {
      note.y = y;
      note.update(this.context);
    });
  }
};